<template>
  <div id="user">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="/home"> 首页 </Breadcrumb-item>
        <Breadcrumb-item> 分类管理 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <i-col span="4">
          <i-button type="primary" @click.native="showNew" style="float: left;">
            <Icon type="plus-round"></Icon>
            添加分类
          </i-button>
        </i-col>
        <i-col span="20">
          <i-form :model="formInline" inline style="float: right;">
            <Form-item>
              <i-select v-model="formInline.status" style="width: 100px;">
                <i-option v-for="item in statusList" :value="item.status">{{ item.name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item>
              <i-button type="success" @click.native="handleSubmit()"> 搜索 </i-button>
            </Form-item>
          </i-form>
        </i-col>
        <i-col span="24" style="margin-top: 15px;">
          <div class="ivu-table-wrapper">
            <div class="ivu-table ivu-table-border ivu-table-stripe">
              <table cellspacing="0" cellpadding="0" border="0" style="width: 100%;">
                <thead class="ivu-table-header">
                <tr class="ivu-table-row">
                  <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>编号</span></div></th>
                  <th class="ivu-table-column-center" width="20%"><div class="ivu-table-cell"><span>全称(英文名)</span></div></th>
                  <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>简称</span></div></th>
                  <th class="ivu-table-column-center" width="12%"><div class="ivu-table-cell"><span>全称(中文名)</span></div></th>
                  <th class="ivu-table-column-center" width="14%"><div class="ivu-table-cell"><span>所属分类</span></div></th>
                  <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>是否热门</span></div></th>
                  <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>状态</span></div></th>
                  <th class="ivu-table-column-center" width="14%"><div class="ivu-table-cell"><span>操作</span></div></th>
                </tr>
                </thead>
                <tbody class="ivu-table-tbody">
                <tr class="ivu-table-row" v-for="(item, index) in list">
                  <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.id }}</span></div></td>
                  <td class=""><div class="ivu-table-cell"><div>{{ item.name }}</div></div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.short_name }}</span></div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.chinese_name }}</span></div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ getParentName(item.sort_id) }}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">
                    <span class="text-danger" v-if="parseInt(item.hot_is) === 1">热门</span>
                    <span v-else>普通</span>
                  </div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">
                    <span class="text-success" v-if="parseInt(item.status) === 1">正常</span>
                    <span class="text-danger" v-else-if="parseInt(item.status) === 0">锁定</span>
                    <span class="text-grey" v-else>删除</span>
                  </div></td>
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">
                      <div>
                        <button type="button" class="ivu-btn ivu-btn-info ivu-btn-small" @click="showEdit(index)"><span>修改</span></button>
                        <button type="button" class="ivu-btn ivu-btn-error ivu-btn-small" @click="showDelete(index)"><span v-if="parseInt(item.status) === 2">清除</span><span v-else>删除</span></button>
                      </div>
                    </div>
                  </td>
                </tr>
                </tbody>
              </table>
            </div>
          </div>
        </i-col>
        <div class="table-page">
          <Page :total="total" show-elevator @on-change="getList"></Page>
        </div>
      </Card>
    </Row>

    <Modal
      title="分类操作"
      v-model="showModal"
      :mask-closable="false"
      :loading="true"
      @on-ok="postModal"
      width="720">
      <Row>
        <i-col span="20" offset="2">
          <i-form v-model="handleModal" :label-width="100">
            <Form-item v-show="handleModal.id !== 0" label="分类编号">
              <i-input v-model="handleModal.id" disabled></i-input>
            </Form-item>
            <Form-item label="分类英文全称">
              <i-input v-model="handleModal.name"></i-input>
            </Form-item>
            <Form-item label="简称">
              <i-input v-model="handleModal.short_name" placeholder="尽量适应英文"></i-input>
            </Form-item>
            <Form-item label="中文全称">
              <i-input v-model="handleModal.chinese_name" placeholder="尽在后台显示"></i-input>
            </Form-item>
            <Form-item label="所属分类">
              <i-select v-model="handleModal.sort_id">
                <i-option :value="0"> - </i-option>
                <i-option v-for="item in list" :value="item.id">{{ item.chinese_name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item label="热门分类">
              <Radio-group v-model="handleModal.hot_is" type="button" size="small" style="float: left;">
                <Radio label="1">热门</Radio>
                <Radio label="0">普通</Radio>
              </Radio-group>
            </Form-item>
            <Form-item label="状态">
              <Radio-group v-model="handleModal.status" type="button" size="small" style="float: left;">
                <Radio label="1">正常</Radio>
                <Radio label="0">锁定</Radio>
                <Radio label="2" v-if="parseInt(handleModal.status) === 2">删除</Radio>
              </Radio-group>
            </Form-item>
          </i-form>
        </i-col>
      </Row>
    </Modal>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        self: this,
        showModal: false,
        total: 0,
        formInline: {
          status: 1
        },
        handleModal: {},
        statusList: [
          { status: -1, name: '所有状态' },
          { status: 0, name: '锁定' },
          { status: 1, name: '正常' },
          { status: 2, name: '删除' }
        ],
        list: []
      }
    },
    created () {
      this.initList()
    },
    watch: {
      '$watch': 'initList'
    },
    methods: {
      handleSubmit () {
        this.$http.post('/admin_api/sort', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      },
      showNew () {
        this.handleModal = {
          id: 0,
          sort_id: 0,
          hot_is: 0,
          status: 1
        }
        this.showModal = true
      },
      showEdit (index) {
        this.handleModal = this.list[index]
        this.showModal = true
      },
      showDelete (index) {
        this.$Modal.confirm({
          title: '确定删除?',
          content: '那就删除吧~',
          loading: true,
          onOk: () => {
            this.$http.post('/admin_api/sort/delete', { id: this.list[index].id }).then(response => {
              if (response.body.error === 0) {
                this.list[index] = {}
                this.$Message.success(response.body.message)
                this.$Modal.remove()
              } else {
                this.$Notice.error({ 'title': '请求失败', 'desc': response.body.message })
              }
            })
          },
          cancelText: '取消'
        })
      },
      getList (page) {
        this.$http.post('/admin_api/sort?page=' + page, this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      },
      postModal () {
        // 异步修改用户信息
        let url = '/admin_api/sort/new'
        if (this.handleModal.id !== 0) {
          url = '/admin_api/sort/edit'
        }
        this.$http.post(url, this.handleModal).then(response => {
          if (response.body.error === 0) {
            this.$Message.success('对 ' + this.handleModal.name + ' 分类的操作以完成')
            if (this.handleModal.id === 0) {
              this.handleModal = {}
            } else {
              let self = this
              this.list.map(function (item, index) {
                if (item.id === self.handleModal.id) {
                  self.list[index].status = parseInt(self.handleModal.status)
                }
              })
            }
            this.$Modal.remove()
            this.$router.go(0)
          } else {
            this.$Notice.error({ 'title': '请求失败', 'desc': response.body.message })
            this.$Modal.loading = false
          }
        })
      },
      getParentName (pid) {
        if (pid === 0) {
          return '-'
        } else {
          for (let thisKey = 0; thisKey < this.list.length; thisKey++) {
            if (pid === this.list[thisKey].id) {
              return this.list[thisKey].chinese_name
            }
          }
        }
      },
      initList () {
        this.$http.post('/admin_api/sort', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .text-danger {
    color: #ff5c33;
  }
</style>



